<script>
import axios from 'axios'
export default {
  name: 'Home',
    data() {
        return{
            searchText:'',
            IsSearchText: false,
            link:false,
            youtubeid: '',
            youtubelink:'',
            images:[],
            youtubesearchid : [],
            ids:[],
            vedioid:[],
            searching: false,
            resultfound: false,
            snippet: [],
            islive:'',
            result:[],
            length: 0,
            remove:false,
            title:'',
            titles:{},
            apikey:'AIzaSyDLhwTVUlkjw5IaZto7kbB1bH738-SSLC4'
        }
    },
    methods : {
        searchvedio() {
            this.IsSearchText = true;
            if(this.searchText.includes('youtube.com/')){
                 console.log("hello")
              let pos = this.searchText.indexOf('=');
              this.youtubeid = this.searchText.slice(pos+1);
              this.youtubelink = 'https://www.youtube.com/embed/' + this.youtubeid;
              this.link = true;
              this.resultfound = true
              this.titlesearch();
            }else {
                this.apisearch()
            }
        },
        async apisearch() {
            const res = await axios.get(`https://www.googleapis.com/youtube/v3/search?part=snippet&key=${this.apikey}&type=vedio&q=${this.searchText}`)
            let i =0;
            for(i=0;i<=5;i++) {
                this.ids[i] = res.data.items[i]
            }
            this.ids = this.ids.filter((obj) => obj?.snippet?.liveBroadcastContent === "none") 
            for(let t = 0;t < this.ids.length; t++){
                 this.titles[t] = this.ids[t].snippet.title
            }
            for(let j = 0 ; j < this.ids.length ; j++) {
               this.youtubesearchid[j] = this.ids[j].id
            }
            let k = 0;
            for(k=0 ; k < this.ids.length ; k++) {
                this.vedioid[k] = this.youtubesearchid[k].videoId
            }
            this.length = this.ids.length
            if(this.length === 3) {
                 this.remove = true
            }
            if(this.length > 4) {
                 this.vedioid.pop()
            }
            console.log(this.vedioid)
            console.log(this.ids)
            this.searching = true
            this.resultfound = true
        },
        async titlesearch() {
             const res = await axios.get(`https://www.youtube.com/oembed?url=http://www.youtube.com/watch?v%3D${this.youtubeid}&format=json`)
             this.title = res.data.title
             console.log(this.title)
        }
    },
    watch : {
        searchText : function () {
            if(this.searchText == '') {
                this.resultfound = false
                this.link = false
                this.searching = false
            }
        }
    }
}
</script>

